import React, { useEffect, useState } from "react";
import { Heading, Anchor, Accordion, AccordionPanel, Box, Text, TextInput } from "grommet";
import { fetchProducts, fetchStoreById, bid } from "../helpers";
import CardListing from "../components/CardList";
import Loading from "../components/Loading";
import Chat from './Chatroom'
import EventObserver from './EventObserver'
import ReactHtmlParser from 'react-html-parser';
import { getLatestBidder, getStoreBidInformation } from '../library/parse'
import { AddCircle, Bundle } from 'grommet-icons'
import { format } from 'date-fns'
const Home = ({
  location,
  pathContext,
  pageContext,
  storeName,
  id,
  ...props
}) => {
  const [products, setProducts] = useState([]);
  const [store, setStore] = useState(null);
  const [messages, setMessages] = useState([]);
  const [event, setEvent] = useState(null)
  const [bidPrices, setBidPrices] = useState({})
  useEffect(() => {

    fetchStoreById(id).then((e) => {
      setStore(e);
    });
    fetchProducts(id).then((e) => setProducts(e));

  }, [id]);


  useEffect(() => {
    if(event) {
      const message = event.get('customField2');
      const createdAt = format(event.get('createdAt'), 'hh:mm');
      event && setMessages(messages.concat([{ message: `${createdAt} - ${message}` }]))
    }    
  }, [event]);
  return (
    <>
      {
        !products.length && <Loading />
      }
      <Heading level={3}> { store && store.get('name') } </Heading>

      <CardListing
        dataList={products.map((e) => ({
          title: e.get("name"),
          thumbnail: e.get('photo') && e.get('photo').url(),
          cta: (
            <>
              <Text>Current Bid: $9</Text>
              <Text>Start Bid: ${e.get('startBid')}</Text>
              <TextInput 
                placeholder="Bid Amount"
                margin="small"
                reverse={true}
                onChange={event => setBidPrices({
                  ...bidPrices,
                  [e.id]: event.target.value
                })}
              />
              <AddCircle 
                  onClick={() => { bid(e.id, bidPrices[e.id]) }}
                />
            </>
          ),
        }))}
      />

<Accordion>
        <AccordionPanel label={store && ReactHtmlParser(`<div>${store.get("description")}</div>`)}>
          <Box pad="medium" background="light-2">
            <EventObserver onCreateEvent={(event) => {
              setEvent(event)
            }} />
            <Chat messages={messages} />
          </Box>
        </AccordionPanel>
      </Accordion>
    </>
  );
};

export default Home;
